@import "../../node_modules/swiper/swiper-bundle.css";
:root {
  --font-family: "Montserrat", sans-serif;
  --content-width: 1446px;
  --content-width-narrow: 1176px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --container-width-narrow: calc(var(--content-width-narrow) + (var(--container-offset) * 2));
  --accent-color: #de596d;
  --bg-color: #fff;
  --red-hover: #bc1340;
  --black-color: #161516;
  --dark-grey-color: #222022;
  --pink: #dd8e94;
  --pink-color: #ffd7da;
  --pink-light: #fff4f8;
  --white-color: #fff;
  --grey-border: #cac8ca;
}

:root[data-theme=dark] {
  --bg-color: #38444c;
  --accent-color: #293238;
  --red-hover: #de596d;
  --pink-light: #697278;
  --pink-color: #5c5858;
  --black-color: #fff;
  --dark-grey-color: #de596d;
  --white-color: #697278;
}

/* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

