html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

ul,
ol {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

ul,
li {
  list-style: none;
}

img {
  height: auto;
  max-width: 100%;
  display: block;
  object-fit: cover;
}

input,
button,
textarea,
select {
  font: inherit;
  border: none;
}

button {
  color: inherit;
  cursor: pointer;
  background-color: transparent;
  border: none;
  padding: 0;
}

a {
  text-decoration-color: transparent;
  color: inherit;
}

address {
  font-style: normal;
}

html,
body {
  height: 100%;
}

body {
  background-color: var(--bg-color);
  color: var(--black-color);
  display: flex;
  flex-direction: column;
  font-family: var(--font-family, sans-serif);
  font-size: 18px;
  line-height: 1.5;
}

.lock {
  overflow: hidden;
  touch-action: none;
}

.page {
  scroll-behavior: smooth;
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
}

.main {
  flex-grow: 1;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.container {
  max-width: var(--container-width);
  padding: 0 var(--container-offset);
  margin: 0 auto;
}
.container--narrow {
  max-width: var(--container-width-narrow);
}

.logo__img {
  object-fit: contain;
}

.sr-only {
  border: 0;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  padding: 0;
  position: absolute;
  height: 1px;
  margin: -1px;
  overflow: hidden;
  white-space: nowrap;
  width: 1px;
}

.visually-hidden {
  clip: rect(0 0 0 0);
  overflow: hidden;
  position: absolute;
  height: 1px;
  width: 1px;
}

.burger {
  display: none;
  position: relative;
  width: 44px;
  height: 44px;
  z-index: 3;
}
@media only screen and (max-width: 991px) {
  .burger {
    display: block;
  }
}
.burger__line, .burger:before, .burger:after {
  content: "";
  background-color: var(--red-hover);
  position: absolute;
  width: 100%;
  height: 2px;
  width: 30px;
  left: 7px;
  transition: transform 0.4s;
}
.burger:before {
  top: 12px;
}
.burger:after {
  bottom: 10px;
}
.burger--active::before {
  transform: rotate(45deg);
  top: 21px;
}
.burger--active::after {
  transform: rotate(-45deg);
  bottom: 21px;
}
.burger--active .burger__line {
  transform: scale(0);
}

.search-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  outline: transparent;
}
.search-btn:focus svg {
  fill: var(--red-hover);
}
@media (any-hover: hover) {
  .search-btn:hover svg {
    fill: var(--red-hover);
  }
}
.search-btn svg {
  width: 24px;
  height: 24px;
  fill: var(--accent-color);
  transition: fill 0.3s ease 0s;
}

.btn {
  padding: 17px 60px;
  border: 1px solid transparent;
  border-radius: 50px;
  outline: transparent;
}
.btn--transparent {
  padding: 13px 40px;
  border-color: var(--accent-color);
  transition: color 0.3s ease 0s, background-color 0.3s ease 0s, box-shadow 0.3s ease 0s;
}
.btn--transparent:focus {
  color: var(--white-color);
  background-color: var(--red-hover);
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.25);
}
@media (any-hover: hover) {
  .btn--transparent:hover {
    color: var(--white-color);
    background-color: var(--accent-color);
  }
}
.btn--red {
  color: var(--white-color);
  background-color: var(--accent-color);
  padding: 17px 60px;
  transition: background-color 0.3s ease 0s, box-shadow 0.3s ease 0s;
}
.btn--red:focus {
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.25);
}
@media (any-hover: hover) {
  .btn--red:hover {
    background-color: var(--red-hover);
  }
}

@media (any-hover: hover) {
  .slider-button-prev:hover svg,
.slider-button-next:hover svg {
    fill: var(--accent-color);
  }
}
.slider-button-prev svg,
.slider-button-next svg {
  fill: var(--black-color);
  width: 55px;
  height: 5px;
  transition: fill 0.3s ease 0s;
}
@media (any-hover: hover) {
  .slider-button-prev--white:hover svg,
.slider-button-next--white:hover svg {
    fill: var(--dark-grey-color);
  }
}
.slider-button-prev--white svg,
.slider-button-next--white svg {
  fill: var(--white-color);
}

.slider-button-prev svg {
  transform: rotate(180deg);
}

.title {
